// main page elements
body {
	font-size: 14px;
	color: $blue;
	font-family: Roboto;
	font-style: normal;
	font-weight: 400;
	background: #E5E5E5;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden;
	background: #E5E5E5; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

// Start page main
.flex {
	display: flex; }
.btn {
	display: flex;
	display: -webkit-box;
	align-items: center;
	justify-content: center;
	width: 100%;
	cursor: pointer;
	text-align: -webkit-center;
	text-align: center; }
.pass-recovery {
	height: 100vh;
	background: #E5E5E5;
	display: flex;
	align-items: center;
	justify-content: center;
	.pass-recovery__wrap {
		max-width: 570px;
		width: 100%;
		background: white;
		padding-top: 50px;
		padding-bottom: 70px;
		border-radius: 40px;
		.pass-recovery__title {
			font-size: 24px;
			line-height: 28px;
			text-align: center;
			color: $blue;
			margin-bottom: 20px; }
		form {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			.error {
				font-size: 20px;
				line-height: 23px;
				text-align: center;
				color: #D52B1E;
				margin-bottom: 30px; }
			.incorrect {
				border: 2px solid #D52B1E;
				color: $blue; }
			.incorrect::-webkit-input-placeholder {
				color: #D52B1E; }
			.incorrect::-moz-placeholder {
				color: #D52B1E; }
			.incorrect:-moz-placeholder {
				color: #D52B1E; }
			.incorrect:-ms-input-placeholder {
				color: #D52B1E; }
			input {
				border: 2px solid $blue;
				border-radius: 40px;
				max-width: 470px;
				width: 100%;
				height: 65px;
				font-size: 22px;
				line-height: 26px;
				text-align: center;
				color: $blue;
				margin-bottom: 40px; }
			input::-webkit-input-placeholder {
				color: $blue; }
			input::-moz-placeholder {
				color: $blue; }
			input:-moz-placeholder {
				color: $blue; }
			input:-ms-input-placeholder {
				color: $blue; }
			.pass-recovery__btn {
				cursor: pointer;
				background: $blue;
				border-radius: 40px;
				max-width: 470px;
				width: 100%;
				height: 65px;
				font-size: 22px;
				line-height: 26px;
				text-align: center;
				color: #FFFFFF;
				display: flex;
				align-items: center;
				justify-content: center; } } } }
