.sign {
	display: flex;
	background: #E5E5E5;
	padding-left: 220px;
	padding-right: 200px;
	padding-top: 20px;
	justify-content: space-between;
	padding-bottom: 0px;
	.right {
		max-width: 664px;
		max-height: 664px;
		margin-top: 118px; }
	.sign__form {
		background: white;
		border-radius: 25px;
		width: 570px;
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		padding-bottom: 5px;
		.sign__logo {
			max-width: 218px;
			width: 100%;
			height: 58px;
			margin-top: 30px;
			margin-bottom: 30px; }
		hr {
			border: 1px solid $blue;
			width: 100%; }
		.dop {
			max-width: 438px;
			width: 100%; }
		form {
			margin-top: 40px;
			display: flex;
			justify-content: center;
			flex-direction: column;
			align-items: center;
			max-width: 470px;
			width: 100%;
			input {
				padding-left: 10px;
				margin-bottom: 40px;
				width: 100%;
				height: 65px;
				border-radius: 40px;
				border: 2px solid $blue;
				font-size: 22px;
				line-height: 26px;
				text-align: center;
				color: $blue;
				text-align: -webkit-center; }
			input::-webkit-input-placeholder {
				color: $blue;
				text-align: -webkit-center; }
			input::-moz-placeholder {
				color: $blue; }
			input:-moz-placeholder {
				color: $blue; }
			input:-ms-input-placeholder {
				color: $blue; }
			.btn {
				height: 65px;
				background: $blue;
				border-radius: 40px;
				font-size: 22px;
				line-height: 26px;
				text-align: center;
				color: #FFFFFF;
				margin-bottom: 40px; } }
		.sign__remember {
			cursor: pointer;
			background: white;
			font-size: 20px;
			line-height: 23px;
			color: $blue;
			margin-bottom: 23px;
			display: flex;
			align-items: center;
			.round {
				position: relative; }
			.round label {
				background-color: #fff;
				border: 1.5px solid $blue;
				border-radius: 50%;
				cursor: pointer;
				height: 21px;
				left: -65px;
				position: absolute;
				display: flex;
				align-items: center;
				margin-top: -20px;
				width: 21px;
				span {
					margin-left: 35px; } }
			.round label:after {
				border: 2px solid $blue;
				border-top: none;
				border-right: none;
				content: "";
				height: 5px;
				left: 3px;
				opacity: 0;
				position: absolute;
				top: 4px;
				transform: rotate(-45deg);
				width: 10px; }
			.round input[type="checkbox"] {
				visibility: hidden; }
			.round input[type="checkbox"]:checked + label {
				background-color: white;
				border-color: $blue; }
			.round input[type="checkbox"]:checked + label:after {
				opacity: 1; } }

		.sign__pass {
				color: $blue;
				text-align: center;
				font-size: 20px;
				line-height: 23px;
				margin-bottom: 40px;
				cursor: pointer;
				background: white; }
		.sign__social {
			display: flex;
			margin-bottom: 11px;
			gap: 40px;
			a {
				img {
					max-width: 42px; } } }
		.create-btn {
			margin-top: 40px;
			color: white;
			background: #159700;
			border-radius: 40px;
			height: 65px;
			max-width: 334px;
			font-size: 22px;
			line-height: 26px; } }
	.sign__text {
		margin-top: 15px;
		display: flex;
		flex-wrap: wrap;
		max-width: 455px;
		a {
			font-size: 18px;
			line-height: 21px;
			color: $gray;
			padding-right: 25px;
			padding-bottom: 10px;
			position: relative;
			&:after {
				position: absolute;
				background: #939597;
				content: '';
				width: 5px;
				height: 5px;
				border-radius: 100%;
				margin-top: 8px;
				margin-left: 10px; } }
		a:last-child {
			&:after {
				display: none; } } } }
